.hero {
    padding: 10rem 0 6rem;
    position: relative;
    overflow: hidden;
    text-align: center;
}

.content {
    max-width: 800px;
    margin: 0 auto;
    position: relative;
    z-index: 10;
}

.title {
    font-size: 4rem;
    line-height: 1.1;
    margin-bottom: 2rem;
    letter-spacing: -2px;
}

.subtitle {
    font-size: 1.25rem;
    color: var(--text-muted);
    margin-bottom: 3rem;
    line-height: 1.6;
}

.actions {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1.5rem;
}

.bgGlow {
    position: absolute;
    top: -10%;
    left: 50%;
    transform: translateX(-50%);
    width: 1000px;
    height: 1000px;
    background: radial-gradient(circle, rgba(99, 102, 241, 0.15) 0%, transparent 70%);
    z-index: 1;
    pointer-events: none;
}

@media (max-width: 1024px) {
    .title {
        font-size: 3.5rem;
    }
}

@media (max-width: 768px) {
    .hero {
        padding: 8rem 0 4rem;
    }

    .title {
        font-size: 2.75rem;
        letter-spacing: -1px;
    }

    .actions {
        flex-direction: column;
        gap: 1rem;
    }

    .actions a {
        width: 100%;
    }
}